@import url(https://fonts.googleapis.com/css?family=Baloo|Caveat|Kaushan+Script|McLaren|Satisfy&display=swap);
* {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

body {
  font-family: "Baloo", cursive;
}

.mainPage {
  height: 100vh;
  display: flex;
  background: url(/amino/static/media/Main.29f318a4.svg) no-repeat;
  background-size: cover;
  background-position: center;
  flex-direction: column;
}
.mainPage .text {
  color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 40%;
  left: 10%;
  font-size: 3em;
}
.mainPage .text .content {
  position: relative;
  font-size: 0.6em;
}
.mainPage .image {
  position: absolute;
  right: 10%;
  top: 30%;
  width: 500px;
}

.aboutUsPage {
  height: 100vh;
  display: flex;
  background: url(/amino/static/media/Main2.23628be3.svg) no-repeat;
  background-size: cover;
  background-position: center;
  flex-direction: column;
}
.aboutUsPage .auTitle {
  color: rgba(0, 0, 0, 0.5);
  width: 40%;
  font-size: 2.5em;
  position: relative;
  top: 25%;
  left: 10%;
  opacity: 0;
}
.aboutUsPage .auContent {
  position: relative;
  width: 50%;
  font-size: 1.5em;
  top: 30%;
  color: rgba(0, 0, 0, 0.5);
  left: 10%;
  z-index: 2;
  opacity: 0;
}
.aboutUsPage .auImage {
  position: absolute;
  right: 5%;
  top: 115%;
  opacity: 0;
  width: 500px;
}
.aboutUsPage .auButton {
  position: relative;
  top: 35%;
  left: 10%;
  opacity: 0;
  width: 20%;
}
.aboutUsPage .auButton > button {
  font-family: "Baloo", cursive;
  color: rgba(0, 0, 0, 0.5);
  border: 3px solid rgba(0, 0, 0, 0.5);
  outline: none;
  background-color: transparent;
  padding: 3% 6%;
  font-size: 1.1em;
}
.aboutUsPage .auButton > button:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}

.whyUsPage {
  background: url(/amino/static/media/Main3.0f99390f.svg) no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
  flex-direction: column;
}
.whyUsPage .wuTitle {
  color: rgba(0, 0, 0, 0.5);
  width: 40%;
  font-size: 2.5em;
  position: relative;
  top: 20%;
  left: 50%;
  opacity: 0;
}
.whyUsPage .wuContent {
  position: relative;
  width: 45%;
  font-size: 1.5em;
  top: 25%;
  color: rgba(0, 0, 0, 0.5);
  left: 50%;
  z-index: 2;
  opacity: 0;
}
.whyUsPage .wuImage {
  position: absolute;
  left: 5%;
  top: 215%;
  opacity: 0;
  width: 500px;
}
.whyUsPage .wuButton {
  position: relative;
  top: 30%;
  left: 50%;
  opacity: 0;
  width: 20%;
}
.whyUsPage .wuButton > button {
  font-family: "Baloo", cursive;
  color: rgba(0, 0, 0, 0.5);
  border: 3px solid rgba(0, 0, 0, 0.5);
  outline: none;
  background-color: transparent;
  padding: 3% 6%;
  font-size: 1.1em;
}
.whyUsPage .wuButton > button:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}

.supportPage {
  background: url(/amino/static/media/Main3.0f99390f.svg) no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  height: 100vh;
  flex-direction: column;
}
.supportPage .sTitle {
  color: rgba(0, 0, 0, 0.5);
  font-size: 3.5em;
  width: 10%;
  position: relative;
  top: 10%;
  left: 25%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 1;
}
.supportPage .faqList {
  list-style-type: none;
  position: absolute;
  left: 25%;
  top: 330%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 5;
}
.supportPage .faqList .listItem {
  width: 100%;
  display: relative;
  top: 1%;
  padding: 2%;
  cursor: pointer;
}
.supportPage .faqList .listItem .question {
  margin-bottom: 7%;
  letter-spacing: 0.05em;
  font-size: 1.2em;
}
.supportPage .faqList .listItem .answer {
  opacity: 0;
  width: 700px;
  position: absolute;
}
.supportPage .sImage {
  position: absolute;
  right: 15%;
  top: 315%;
  width: 500px;
}

.loginPage {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contactPage {
  height: 100vh;
  display: flex;
}

#companyName {
  position: absolute;
  top: 5%;
  left: 5%;
}

.loadingImage {
  position: absolute;
  top: 40%;
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.preload {
  justify-content: center;
  align-content: center;
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 12312;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#fad0c4), to(#ffd1ff));
  background-image: linear-gradient(to top, #fad0c4 0%, #ffd1ff 100%);
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}
.preload.finished {
  opacity: 0;
  pointer-events: none;
}

.navBar {
  position: absolute;
  top: 5%;
  width: 97%;
  list-style-type: none;
  cursor: pointer;
}
.navBar .options {
  float: right;
  padding: 0 3%;
  right: 5%;
  font-size: 1.5em;
  color: rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 414px) {
  #companyName {
    width: 150px;
  }

  .mainPage .text {
    top: 60%;
    left: 5%;
    font-size: 2em;
  }
  .mainPage .text .content {
    position: relative;
    font-size: 0.6em;
  }
  .mainPage .image {
    width: 90%;
    left: 5%;
    top: 25%;
  }

  .aboutUsPage .auTitle {
    width: 50%;
    font-size: 1.5em;
    top: 15%;
    left: 10%;
  }
  .aboutUsPage .auContent {
    width: 90%;
    font-size: 1em;
    top: 60%;
    left: 5%;
    opacity: 0;
  }
  .aboutUsPage .auImage {
    width: 90%;
    right: 5%;
    top: 125%;
    opacity: 0;
  }
  .aboutUsPage .auButton {
    top: 65%;
    left: 10%;
    opacity: 0;
    width: 40%;
  }

  .whyUsPage .wuTitle {
    width: 50%;
    font-size: 1.5em;
    top: 15%;
    left: 10%;
  }
  .whyUsPage .wuContent {
    width: 90%;
    font-size: 1em;
    top: 55%;
    left: 5%;
    opacity: 0;
  }
  .whyUsPage .wuImage {
    width: 90%;
    right: 5%;
    top: 225%;
    opacity: 0;
  }
  .whyUsPage .wuButton {
    top: 60%;
    left: 10%;
    opacity: 0;
    width: 40%;
  }

  .supportPage .sTitle {
    width: 30%;
    font-size: 1.5em;
    right: 10%;
  }
  .supportPage .faqList {
    top: 330%;
    left: 5%;
    -webkit-transform: translate(0);
            transform: translate(0);
    width: 90%;
  }
  .supportPage .faqList .listItem .question {
    margin-bottom: 7%;
    letter-spacing: 0.05em;
    font-size: 1em;
  }
  .supportPage .faqList .listItem .answer {
    opacity: 0;
    font-size: 0.8em;
    width: 90%;
    position: absolute;
  }
  .supportPage .sImage {
    width: 90%;
    left: 5%;
    top: 355%;
    opacity: 0.5;
  }
}
